.Character {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 3fr;
}

.imgContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 156px;
    margin: 8px auto;
}

.imgContainer img {
    border-radius: 88px;
    width: inherit;
    height: inherit;
    margin: auto;
    border: 3px solid black;
    box-shadow: -5px 5px 10px 0px rgba(0,0,0,0.36);
}

.info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    text-align: left;
}

.info div {
    margin: 6px 2px;
}

.meta {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: clamp(16px, 4vw, 40px);
    filter: drop-shadow(-5px 5px 10px rgba(0,0,0,0.36));

}

.meta div {
    margin-right: 10px;
}

.meta img {
    width: clamp(16px, 3.2vw, 32px);
    height: clamp(16px, 3.2vw, 32px);
    margin: 0 2px;
}